import React from "react";
import { useState, useContext, useRef,useEffect } from "react";
import { useNavigate,Link } from "react-router-dom";
import { getCookie } from "../../../../Cookie";
import { FetchUrl } from "../../../../store/communication";
import "./RoomReform.css";
//outputs.status

function RoomReform() {
  //  
  const [inputs, setInputs] = useState({
  });
  const navigate = useNavigate();

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs((values) => ({ ...values, [name]: value }));
  };

  //URL
  const FETCH_URL = useContext(FetchUrl);
  
  
  const id=window.location.pathname.split("/")[2].substring(0 );
  const url1 = `${FETCH_URL}/rooms/`+id+`/settings`;
  const url = `${FETCH_URL}/rooms/`+id+`/update`;
  const imgeRef = useRef();
  useEffect(() => {
    console.log("REFORM START");
  fetch(url1, {
    headers: {
      accessToken: getCookie("accessToken"),
    },
  })
    .then((response) => {
      console.log(response);
      if (response.ok) {
        console.log("resOK");
        return response.json(); //ok  .
      } else {
        response.json().then((data) => {
          console.log("");
          let errorMessage = "";
          throw new Error(errorMessage);
        });
      }
    })
    .then((result) => {
      if (result != null) {
        console.log("resultOK");
        console.log("result.responseData.room");
        setInputs(result.responseData.room);
        if(inputs.roomPwd===null||inputs.roomPwd===""){
          inputs.roomPwd=""}
          
        
        //navigate("/RoomDetail/:" + result.responseData.roomId);
        //window.location.reload(); //
      }
    })
    .catch((err) => {
      console.log(".");
    });
  }, [])
  const handleSubmit = (event) => {
    event.preventDefault();
    const formData = new FormData();
    let outputs={
      roomName: inputs.roomName,
      mode: inputs.mode, //MODE1, MODE2, MODE3
      pwd: inputs.roomPwd,
      roomDescription:inputs.description,
      roomCategory: inputs.categoryName, 
      roomMemberMaxNo: inputs.num,
      endAt: inputs.endTime,
    };
    formData.append(
      "roomUpdateDTO",
      new Blob([JSON.stringify(outputs)], { type: "application/json" })
    );
    if(imgeRef!==null&&imgeRef!==""&&imgeRef.current.files[0]!==undefined){
      formData.append("images", imgeRef.current.files[0])};
    fetch(url, {
      method: "POST",
      body: formData,
      headers: {
        accessToken: getCookie("accessToken"),
      },
    })
    .then((response) => {
      console.log(response);
      if (response.ok) {
        return response.json(); //ok  .
      } else {
        response.json().then((data) => {
          console.log("post .");
          let errorMessage = "";
          throw new Error(errorMessage);
        });
        }
      })
    .then((result) => {
      if (result != null) {
        console.log("");
        navigate("./");
      }
    })
    .catch((err) => {
      console.log(" POST");
    });
  };
  const [fileImage, setFileImage] = useState();
  const saveFileImage = (event) => {
    setFileImage(URL.createObjectURL(event.target.files[0]));
  };
  return (
    <div className="body-frame">
      <form onSubmit={handleSubmit} className="floatRIGHT">
        {/*form input name, type  .   id   . input  .*/}
        <div className="form-frame">
          <div className="room-image">
          <img
            alt="sample"
            src={fileImage?fileImage:inputs.img}
            style={{
              position: "absolute",
              marginTop: "55px",
              width: "150px",
              height: "150px",
              borderRadius: "50%",
              pointerEvents:"none"
            }}
            />
            {/* ,  */}
            <input
              type="file"
              name="roomImage"
              accept="image/*"
              onChange={saveFileImage}
              className="room-image__upload"
              ref={imgeRef}
            />
            <div className="room-image__message">  </div>
          </div>
          <div className="room-infor">
            {/**/}

            <div className="input-type">
              <div className="line">
                <input
                  type="text"
                  name="roomName"
                  value={inputs.roomName || ""}
                  onChange={handleChange}
                  placeholder="  "
                />
              </div>
              <div className="line">
                <input
                  type="text"
                  name="description"
                  value={inputs.description || ""}
                  onChange={handleChange}
                  placeholder="  "
                />
              </div>
              <div className="line">
                <input
                  type="number"
                  name="num"
                  value={inputs.num ? inputs.num : ""}
                  onChange={handleChange}
                  accept="number"
                  placeholder=" (1~25)"
                />
                <select
                  name="categoryName"
                  value={inputs.categoryName || ""}
                  onChange={handleChange}
                >
                  <option value="" disabled>
                     
                  </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div className="line">
                <span></span>
                    <input
                      type="datetime-local"
                      name="endTime"
                      value={inputs.endTime || ""}
                      onChange={handleChange}
                    />

                {/*checkbox   .*/}
                <input
                  type="password"
                  name="roomPwd"
                  value={inputs.roomPwd || ""}
                  onChange={handleChange}
                  maxLength="4"
                  minLength="4"
                  placeholder=" 4"
                />
              </div>
            </div>

            <div className="input-rules">
              {/*.  . */}
              <div className="rules-title"> </div>
              <div className="rules-box">
                <label>
                  <input type="radio" value="MODE1"
                    checked={inputs.mode==="MODE1"?"checked":""}
                    onChange={handleChange}
                    name="mode"/>
                  
                </label>
                <label>
                  <input type="radio" value="MODE2"
                    checked={inputs.mode==="MODE2"?"checked":""}
                    onChange={handleChange}
                    name="mode"/>
                  
                </label>
                <label>
                  <input type="radio" value="MODE3"
                    checked={inputs.mode==="MODE3"?"checked":""}
                    onChange={handleChange}
                    name="mode"/>
                  
                </label>
                <label>
                  <input type="radio" value="MODE4"
                    checked={inputs.mode==="MODE4"?"checked":""}
                    onChange={handleChange}
                    name="mode"/>
                  
                </label>
              </div>
            </div>
            <button type="submit"></button>
          </div>
        </div>
      </form>
    </div>
  );
}

export default RoomReform;
