import React from "react";
import { useContext, useState } from "react";
import { FetchUrl } from "../../../store/communication";

import "./PointAdd.css";
function PointAdd() {
  const FETCH_URL = useContext(FetchUrl);
  const url = `${FETCH_URL}/points/kakao?`;
  const [inputs, setInputs] = useState({
    valueSelect: "1000",
    valueInputs: "",
  });
  function getCookie(name) {
    const cookie = document.cookie
      .split(";")
      .map((cookie) => cookie.split("="))
      .filter((cookie) => cookie[0] === name);
    return cookie[0][1];
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(
      url + "value=" + (inputs.valueSelect === "Free" ? inputs.valueInputs : inputs.valueSelect)
    );
    fetch(
      url + "value=" + (inputs.valueSelect === "Free" ? inputs.valueInputs : inputs.valueSelect),
      {
        method: "POST",
        headers: {
          accessToken: getCookie("accessToken"),
        },
      }
    )
      .then((response) => {
        console.log(response);
        if (response.ok) {
          return response.json(); //ok  .
        } else {
          response.json().then((data) => {
            let errorMessage = "";
            throw new Error(errorMessage);
          });
        }
      })
      .then((result) => {
        if (result != null) {
          window.location.href =result.responseData.REDIRECT_URL;
          localStorage.setItem("tid",result.responseData.tid);
        }
      })
      .catch((err) => {
        console.log("ERR");
      });
  };

  const [isShown, setIsShown] = useState(false);
  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs((values) => ({ ...values, [name]: value }));
    if (value === "Free" && name === "valueSelect") {
      //valueInputs
      setIsShown(true);
    } else if (value !== "Free" && name === "valueSelect") {
      setIsShown(false);
    }
  };

  return (
    <>
      <form className="kakaopay-form-frame" onSubmit={handleSubmit}>
        <div className="point-my-point">
          <div className="point-my-pointvalue">
            <div className="point-my-pointtitle">Point</div>
            <div className="point-my-pointdes">
              <div className="point-my-pointdet">
                <div className="point-my-ownpoint" id="point-my-ownpoint-mine">
                  <div id="point-my-ownpoint-mine1">   </div>
                  <div id="point-my-ownpoint-value1">50000 </div>
                </div>
                <div className="point-my-ownpoint">
                  <div id="point-my-ownpoint-mine2">  </div>
                  <div id="point-my-ownpoint-value2">50000 </div>
                </div>
                <div className="point-my-ownpoint">
                  <div id="point-my-ownpoint-mine2">  </div>
                  <div id="point-my-ownpoint-value2">50000 </div>
                </div>
                <div className="point-my-ownpoint">
                  <div id="point-my-ownpoint-mine2">  </div>
                  <div id="point-my-ownpoint-value2">50000 </div>
                </div>
              </div>
              <div className="point-watchme">
                <div id="point-watchme-title">WatchMe Point..</div>
                <div id="point-watchme-text">
                  WatchMe        . ,
                  ,     WatchMe Point   
                  .
                </div>
              </div>
            </div>
          </div>
          <div className="point-log">
            <div className="point-list"> </div>
            <div className="point-table">
              <table>
                <thead>
                  <tr className="point-table-align">
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr className="point-table-align">
                    <td>2022-08-13</td>
                    <td>Sprint </td>
                    <td>-500</td>
                  </tr>
                  <tr>
                    <td>2022-08-13</td>
                    <td>Sprint </td>
                    <td>-500</td>
                  </tr>
                  <tr>
                    <td>2022-08-13</td>
                    <td>Sprint </td>
                    <td>-500</td>
                  </tr>
                  <tr>
                    <td>2022-08-13</td>
                    <td>Sprint </td>
                    <td>-500</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div>
          <div className="point-cash-title"></div>
          <div className="selection">  </div>
          <div className="kakaopay-pay1">
            <input type="button" className="kakaopay-pay-item" value="" />
            <input type="button" className="kakaopay-pay-item" value="" />
            <input type="button" className="kakaopay-pay-item" value="" />
          </div>
          <div className="kakaopay-pay1">
            <input type="button" className="kakaopay-pay-item" value="" />
            <input type="button" className="kakaopay-pay-item" value="" />
            <input type="button" className="kakaopay-pay-item" value="" />
          </div>
          <div className="kakaopay-money">
             
            <input placeholder=" ." />
          </div>
          <div className="money-selection">  </div>
          <div className="kakaopay-money-btn">
            <input value="+ 1" type="button" className="kakaopay-money-btn-item" />
            <input type="button" className="kakaopay-money-btn-item" value="+ 3"></input>
            <input type="button" className="kakaopay-money-btn-item" value="+ 5"></input>
            <input type="button" className="kakaopay-money-btn-item" value="+ 10"></input>
          </div>
        </div>
      </form>
    </>
  );
}

export default PointAdd;
